.page-container {
  margin: 0;
  height: 100vh;
  box-sizing: border-box;
  overflow: hidden;
  /* background: linear-gradient(to right, rgb(6, 57, 119), rgb(0, 120, 255)); */

  width: 100%;

  background-size: cover;
}


.background-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url("../images/login/Colored\ Shapes.svg");
  background-repeat: no-repeat;
  background-size: 100vw 50vh;
}

.content-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.container {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  position: absolute;
  overflow: hidden;
  width: 40vw;
  height: 40vh;
}

.leftContainer {
  width: 50%;
  padding: 0 1vw;
  height: 100%;
  background: #ff416c;
  /* background: -webkit-linear-gradient(to right, #ff4b2b, #ff416c); */
  background: linear-gradient(to right, #ff4b2b, #ff416c);
  color: #ffffff;
  background-size: cover;

  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.signInContainer {
  width: 50%;
  padding: 1vw;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.leftContainer img {
  width: 10vw;
}

.leftContainer p {
  text-wrap: wrap;
  font-size: 0.8em;
  text-align: center;
}

form {
  background: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  height: 100%;
}

h2 {
  font-weight: bold;
  margin: 0;
}

h3 {
  margin: 0;
}

h5 {
  margin: 0 0 15px 0;
  text-align: center;
}

input {
  background-color: #eee;
  border: none;
  padding: 0.7em 2em;
}

.inputField,
.passwordField {
  display: flex;
  margin: 0.5vw 0;
  width: 100%;
  gap: 0.5vw;
}

.signIn {
  border-radius: 20px;
  border: 1px solid #ff4b2b;
  background-color: #ff4b2b;
  color: #ffffff;
  font-size: 0.75em;
  font-weight: bold;
  padding: 0.7em 2.9em;
  margin: 1.3vh 2vw;
  margin-top: 1vh;
  letter-spacing: 0.09em;
  text-transform: uppercase;
  transition: transform 80ms ease-in;
  cursor: pointer;

  &:active {
    transform: scale(0.95);
  }

  &:focus {
    outline: none;
  }
}

.checkbox {
  width: 90%;
  display: flex;
  font-size: 0.7em;
  margin-left: 2vw;
}

p {
  font-size: 0.9em;
  line-height: 1.2em;
  letter-spacing: 0.04em;
}

.errorMessage {
  font-size: 0.8em;
  text-align: center;
  color: red;
}

.header {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  text-align: center;
  position: absolute;
  top: 20%;
  font-size: 1em;
  line-height: 2em;
  color: transparent;
  -webkit-text-stroke: 1px #fff;
  background: url("../images/login/back.png");
  -webkit-background-clip: text;
  background-position: 0 0;
  animation: back 20s linear infinite;
}

@keyframes back {
  100% {
    background-position: 2000px 0;
  }
}

.footer {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  text-align: center;
  position: absolute;
  bottom: 0%;
  background: linear-gradient(to right, #ff4b2b, #ff416c);
  color: #fff;
  height: 10vh;
}

.background-container {
  animation: background-container 20s linear infinite alternate both;
}

/*Animation background image login*/
@keyframes background-container {
  0% {
    transform: scale(1) translateY(0);
    transform-origin: 50% 16%;
  }

  100% {
    transform: scale(1.25) translateY(-15px);
    transform-origin: top;
  }
}

/*Media Queries*/
/*For Small Screens and down*/
@media (max-width: 600.98px) {
  .container {
    width: 80vw;
    height: 80vh;
    flex-direction: column;
  }

  .leftContainer {
    width: 100%;

    text-wrap: pretty;
  }

  .leftContainer img {
    width: 35vw;
  }

  .leftContainer p {
    text-wrap: wrap;
    font-size: 0.7em;
    text-align: center;
  }

  .signInContainer {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }

  .signIn {
    margin-top: 2vh;
  }

  .inputField,
  .passwordField {
    margin: 1.3vw 0;
  }

  .checkbox {
    width: 90%;
    display: flex;
    font-size: 0.7em;
    margin-left: 5vw;
  }

  h3 {
    margin: 0;
  }

  h5 {
    margin: 0 0 15px 0;
    text-align: center;
  }

  .background-container {
    animation: none;
  }

  .header,
  .footer {
    display: none;
  }
}

